	module.exports = {"data":{"markdownRemark":{"html":"<p>Go to Qingpu to attend a seminar to discuss Shanghai's strategy of developing an international metropolis. Shanghai has achieved world-recognized achievements in the past three years. The city's appearance and economic development have undergone tremendous changes. Many old Shanghai and Shanghai have also lamented. People's mentality has been steadily in this rapid development process, which is an important atmosphere for social stability. What is going to be discussed now is how to do in Shanghai in the next three years? Everyone has a warm discussion, vivid opinions and creativity.</p>\n<p>The development of a modern metropolis should have two wings. First, strong economic strength, Shanghai's goal is economic center, trade center and financial center. The second is to have culture and creativity. Fundamentally, the most attractive place in Shanghai in recent years is her ability to innovate, mainly institutional innovation. This is the most attractive place in the city. People come to the city, or people are willing to live in a city or a society, the main temptation is its creative power, she can</p>\n<p>Give people the creative power and the creative power that she can inspire. If you are strong, it will be attractive. On the contrary, it will be unattractive. The future development of Shanghai can choose:</p>\n<p>From external development to internal development</p>\n<p>From the development of things to the development of human beings</p>\n<p>From domain development to institutional development</p>\n<p>From iconic development to decorative development</p>\n<p>It is necessary to propose a higher artistic conception, such as \"human life circle\" or \"big city life circle\", \"new urban life circle\". The Japanese have already had such a reference in the past few years. There must be new pursuits because we are already moving towards the new century.</p>\n<p>The changes that will take place in the new century are hard to imagine today.</p>","id":"/home/swain/workspace/political-life/src/pages/en/0186.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/en/0186.md","frontmatter":{"title":"November 15th Tuesday"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/en/0186.md absPath of file >>> MarkdownRemark"}}

